* {
  padding: 0;
  margin: 0;
  list-style: none;
  outline: none;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}
.App {
  background-color: #e5e5e5;
  max-width: 1920px;
  margin: 0 auto;
}
